module.exports = "/* ------------------------------------------------------\n\n       5.2. Breadcrumb\n\n   ------------------------------------------------------ */\n.nv-breadcrumb-area {\n  position: relative;\n  z-index: 1;\n  padding: 50px 0px;\n  overflow: hidden;\n}\n.nv-breadcrumb-area .container {\n  z-index: 3;\n  position: relative;\n}\n.nv-breadcrumb-image {\n  background-image: url('breadcrumb_img.jpg');\n  width: 59.67%;\n  height: 300px;\n  position: absolute;\n  right: 0;\n  top: 0;\n  bottom: 0;\n  z-index: 1;\n}\n.nv-breadcrumb-title {\n  font-family: \"Montserrat\", sans-serif;\n  color: #f1f1f1;\n  font-size: 9em;\n  line-height: 1em;\n  font-weight: 700;\n  text-transform: uppercase;\n  position: absolute;\n  top: 18.66%;\n  left: 7%;\n  white-space: nowrap;\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -o-user-select: none;\n  -ms-user-select: none;\n      user-select: none;\n}\n.nv-breadcrumb-container {\n  display: inline-block;\n  min-width: 360px;\n  max-width: 550px;\n  border-width: 3px 3px 3px 0;\n  padding: 58px 30px 58px 0;\n  border-style: solid;\n  border-color: #9a9191;\n  margin-left: 17.1%;\n  margin-right: 17.1%;\n  position: relative;\n  z-index: 3;\n}\n.nv-breadcrumb-container::before {\n  content: \"\";\n  display: block;\n  border-width: 37px 0 37px 0;\n  border-style: inherit;\n  border-color: inherit;\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 3px;\n  height: 100%;\n}\n.nv-breadcrumb {\n  position: relative;\n  z-index: 2;\n}\n.nv-breadcrumb > li {\n  display: inline-block;\n  color: #9a9191;\n}\n.nv-breadcrumb > li:not(:last-child)::after {\n  content: \"/\";\n  display: inline-block;\n  margin-left: 5px;\n  margin-right: 1px;\n}\n.nv-breadcrumb a {\n  color: #9a9191;\n}\n.nv-breadcrumb a:hover {\n  color: #e5a01e;\n}\n/* ------------------------------------------------------\n\n    14. Media Queries\n\n------------------------------------------------------ */\n@media only screen and (max-width: 1199px) {\n  .nv-breadcrumb-title {\n    font-size: 7em;\n  }\n}\n@media only screen and (max-width: 991px) {\n  .nv-breadcrumb-title {\n    font-size: 6em;\n  }\n}\n@media only screen and (min-width: 768px) and (max-width: 1200px) {\n  .nv-widget .nv-entries .nv-entry-thumb-area {\n    max-width: 40%;\n    margin-right: 15px;\n  }\n\n  .nv-widget .nv-entries .nv-entry-meta:not(:last-child) {\n    margin-bottom: 5px;\n  }\n}\n@media only screen and (min-width: 768px) and (max-width: 991px) {\n  [class*=row] > [class*=col-]:not([class*=col-sm]):not([class*=col-xs]):not(:last-child) {\n    margin-bottom: 45px;\n  }\n}\n@media only screen and (max-width: 767px) {\n  .nv-breadcrumb-title {\n    font-size: 5em;\n  }\n\n  .nv-breadcrumb-container {\n    padding-top: 40px;\n    padding-bottom: 40px;\n    margin-left: 10%;\n    margin-right: 10%;\n  }\n\n  .nv-breadcrumb-image {\n    width: 100%;\n    background-image: url('breadcrumb_img2.jpg');\n  }\n\n  .nv-breadcrumb-image::after {\n    content: \"\";\n    display: block;\n    position: absolute;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    background: rgba(255, 255, 255, 0.7);\n  }\n}\n@media only screen and (max-width: 500px) {\n  .nv-breadcrumb-container {\n    min-width: initial;\n    display: block;\n    margin-left: 20px;\n    margin-right: 20px;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"